.individual-img {
	width: 60px;
	height: 60px;
	object-fit: contain;
	border-radius: 50%;
}

.business-img {
	width: 54px;
	height: 40px;
	object-fit: cover;
	border-radius: 4px;
}

.view-list {
	width: fit-content;
	height: 40px;
	line-height: 40px;
}

.agent-img {
	object-fit: cover;
	border-radius: 50%;
}

.dealer-summary {
	background-color: var(--general-1);
	border-radius: 4px;
}

.mat-icon.phone-icon {
	display: inline-block !important;
	width: 18px;
	height: 18px;
	vertical-align: middle;
	margin-inline-end: 8px;
	font-size: 18px;
}

.mw-343px {
	max-width: 343px;
}
